import MassTable from "../table/MassTable";

export default class MassItem_Dict {
  items
  get Items() {
    return this.items;
  }
  set Items(v) {
    this.items = v;
    this.MassTable.Items = v;
  }

  massTable
  get MassTable() {
    if (!this.massTable) {
      this.massTable = new MassTable();
    }
    return this.massTable;
  }

  set MassGroupSetting(v)  {
    this.MassTable.MassGroupSetting = v;
  }
}