import RebarMass_Dict from '../mass/rebar/RebarMass_Dict';
import * as phaseDictUtil from '../phase/Phase_DictUtil'
import * as util from './ProjectUtil'
import * as typicalLevelUtil from '../setting/typicallevel/TypicalLevelSettingUtil'

export default class Project {
  phase_Dict
  get Phase_Dict() {
    if (!this.phase_Dict) {
      this.phase_Dict = phaseDictUtil.get(this);
    }
    return this.phase_Dict;
  }

  rebarMass_Dict
  get RebarMass_Dict() {
    if (!this.rebarMass_Dict) {
      this.rebarMass_Dict = util.GetRebarMass_Dict(this);
    }
    return this.rebarMass_Dict;
  }

  inputExportSet
  get InputExportSet() {
    if (!this.inputExportSet) {
      util.GetInputExportSet_Async(this);
    }
    return this.inputExportSet;
  }
  set InputExportSet(v) {
    this.inputExportSet = v;
    this.GetInputExportSetDone = true;
  }

  OnGetInputExportSetDone_Funcs = []

  getInputExportSetDone
  get GetInputExportSetDone() {
    return this.getInputExportSetDone;
  }
  set GetInputExportSetDone(v) {
    this.getInputExportSetDone = v;
    this.OnGetInputExportSetDone_Funcs.forEach(x => x());
  }

  level_Dict
  get Level_Dict() {
    if (!this.level_Dict) {
      this.level_Dict = util.GetLevel_Dict(this);
    }
    return this.level_Dict;
  }

  OnGetTypicalLevelSetting_Funcs = []

  set GetTypicalLevelSetting(v) {
    this.OnGetTypicalLevelSetting_Funcs.forEach(x => x());
  }

  IsOnGetDb_Async

  typicalLevelSetting
  get TypicalLevelSetting() {
    if (!this.typicalLevelSetting) {
      if (!this.IsOnGetDb_Async) {
        this.IsOnGetDb_Async = true;
        typicalLevelUtil.GetFromDb_Async(this, null, 
          (resData, editData) => {
          this.TypicalLevelSetting = resData;
          this.TypicalLevelSetting_ForEdit = editData;
          this.GetTypicalLevelSetting = true
        })
      }
    }
    return this.typicalLevelSetting;
  }
  set TypicalLevelSetting(v) {
    this.typicalLevelSetting = v;
  }

  typicalLevelSetting_ForEdit
  get TypicalLevelSetting_ForEdit() {
    if (!this.typicalLevelSetting_ForEdit) {
      this.typicalLevelSetting_ForEdit = this.TypicalLevelSetting;
    }
    return this.typicalLevelSetting_ForEdit;
  } set TypicalLevelSetting_ForEdit(v) {
    this.typicalLevelSetting_ForEdit = v;
  }

  url
  get Url() {
    if (!this.url) {
      var isHash = true;
      var obj = `${process.env.PUBLIC_URL}${isHash ? '/#' : ''}/project`;
      obj = `${obj}/${this._id}`;

      this.url = obj;
    }
    return this.url;
  }
}