import * as util from './ExportSetUtil'

export class ExportSet {
  currentVersion
  get CurrentVersion(){
    if (!this.currentVersion){
      util.GetCurrentVersion_Async(this);
    }
    return this.currentVersion;
  }
  set CurrentVersion(v){
    this.currentVersion = v;
    this.GetCurrentVersionDone = v;
  }

  OnGetCurrentVersionDone_Func =[]

  set GetCurrentVersionDone(v){
    this.OnGetCurrentVersionDone_Func.forEach(x => x());
  }

  AddVersion = () => {
    util.AddVersion_Async(this);
  }
}