import * as util from './MassItemUtil'
import * as mif from './MassItemField'
import { message } from 'antd'
import * as api from '../../../../../util/apiUtil'

export default class MassItem {
  Value
  Unit

  projectId

  project
  get Project() {
    if (!this.project) {
      this.project = util.GetProject(this);
    }
    return this.project;
  }

  workpackageId

  workpackage
  get Workpackage() {
    if (!this.workpackage) {
      this.workpackage = util.GetWorkpackage(this);
    }
    return this.workpackage;
  }

  phases
  get Phases() {
    if (!this.phases) {
      this.phases = util.GetPhases(this);
    }
    return this.phases;
  }

  get ValidPhasesByInput() {
    return util.GetValidPhasesByInput(this);
  }

  IsGetElement = false

  OnGetElementDone_Funcs = []

  set GetElementDone(v) {
    this.OnGetElementDone_Funcs.forEach(x => x());
  }

  GetValue = (field) => {
    if (!field) {
      return;
    }

    var data = mif.GetFullFields(this, field);
    var { fields, isFromElement } = data;

    var getValFunc = () => {
      var obj = this;
      var isValid = true;

      fields.forEach(x => {
        if (isValid) {
          var temp = obj[x];
          obj = temp;
          if (obj === undefined) {
            isValid = false;
          }
        }
      })

      return obj;
    }

    if (!isFromElement || this.Element) {
      return getValFunc();
    } else {
      return undefined;
    }
  }

  get ApiData() {
    return {
      _id: this._id,
      projectId: this.projectId,
      elementId: this.elementId,
      workpackageId: this.workpackageId,
      exportSetId: this.exportSetId,
      exportSetVersionId: this.exportSetVersionId,
      value: this.Value,
      unit: this.Unit,
      massType: this.MassType,
      info: this.Info
    }
  }

  AddDatabase = (handle) => {
    var apiData = this.ApiData;
    var { _id } = apiData;
    var url = `https://api.bimdev.vn/api/hbm`;

    var command = _id ? 'edit' : 'add';
    const apiFunc = () => _id ?
      api.put(`${url}/${command}`, apiData) : api.post(`${url}/${command}`, apiData);

    apiFunc().then(response => response.data)
      .then(data => {
        if (command === 'add') {
          this._id = data._id;
        }
        if (handle) {
          handle(data);
        }
        message.success(`${command} mass successfully`);
      })
  }

  DeleteDatabase = (handle) => {
    var apiData = {
      _id: this._id
    };
    var url = `https://api.bimdev.vn/api/hbm`;
    const apiFunc = () => api.put(`${url}/delete`, apiData);

    apiFunc().then(response => response.data)
      .then(data => {
        if (handle) {
          handle(data);
        }
        message.success(`delete mass successfully`);
      })
  }

  IsGet_TLS_LevelGroup = false
  OnGet_TLS_LevelGroup_Func_Dict = []

  get Get_TLS_LevelGroup(){
    return this.get_TLS_LevelGroup
  }
  set Get_TLS_LevelGroup(v) {
    this.OnGet_TLS_LevelGroup_Func_Dict.forEach(x => x.Value());
    this.get_TLS_LevelGroup = v;
  }

  tls_LevelGroup
  get TLS_LevelGroup() {
    if (!this.tls_LevelGroup) {
      util.Handle_TLS_LevelGroup(this);
    }
    return this.tls_LevelGroup;
  } set TLS_LevelGroup(v) {
    this.tls_LevelGroup = v;
    this.Get_TLS_LevelGroup = true;
  }

  OnGet_FullValue_Func_Dict = []
  set Get_FullValue(v) {
    this.OnGet_FullValue_Func_Dict.forEach(x => x.Value())
    this.get_FullValue = v;
  }

  // bao gm gi tr tng in hnh
  fullValue
  get FullValue() {
    if (!this.fullValue) {
      util.Handle_FullValue(this);
    }
    return this.fullValue;
  } set FullValue(v) {
    this.fullValue = v;
    this.Get_FullValue = true;
  }

  phaseItems
  get PhaseItems(){
    if (!this.phaseItems){
      this.phaseItems = util.GetPhaseItems(this);
    }
    return this.phaseItems;
  }

  GetPhaseItem = (phaseName) => {
    var list = this.PhaseItems;
    var index = list.findIndex(x => x.Phase.Name === phaseName);
    return list[index];
  }
}