import * as api from '../../../../util/apiUtil'
import { Level } from "./Level"

export const get = (data, dict) => {
  var qI = new Level();
  qI.Name = data.name;
  qI.Elevation = data.elevation;
  qI.Dict = dict;
  return qI;
}

export const getAll = (apiData, dict, handle) => {
  var url = "https://api.bimdev.vn/api/hbl/list";
  api.post(url, apiData).then(response => response.data)
    .then(data => {
      if (!data) data = []
      var items = data.map(x => get(x, dict))
      if (handle) {
        handle(items);
      }
    });
}