import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Select } from "antd";
import TableVM from "./TableVM";
import * as rs from './RenderState'

const { Option } = Select;

class TableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new TableVM();
    } else {
      this.state = store;
    }
  }

  onValueChange = (v) => {
    var data = this.state;
    data.SelectedIndex = v;
  }

  render() {
    var data = this.state;
    var columns = data.Columns;

    var rows = data.Rows;
    var tableElem = '';
    if (columns && rows) {
      var theadElem = <thead className="thead-light">
        <tr>
          {columns.map(x => {
            return <th scope="col" key={x.Key}>{x.Title}</th>
          })}
        </tr>
      </thead>

      var tbodyElem = <tbody>
        {rows.map(x => {
          return <tr key={x.Key}>
            {x.Cells.map(y => {
              return <td key={y.Key}>{y.Render()}</td>
            })}
          </tr>
        })}
      </tbody>

      tableElem = <table className="table table-sm">
        {theadElem}{tbodyElem}
      </table>
    }

    return (
      <>
        {tableElem}
      </>
    )
  }
}

export default observer(TableView)