import { makeObservable, observable, action } from 'mobx';
import * as textVMUtil from '../../../../text/TextVMUtil'
import * as api from '../../../../../util/apiUtil'
import * as genUtil from '../../../../../util/generalUtil'
import * as mif from '../item/MassItemField'
import { PMData } from '../../../single/model/PMData';
import * as util from './MassCellUtil'
import TextView from '../../../../text/TextView';

export const pmData = () => {
  return PMData.Instance;
}

export default class MassCell {
  constructor() {
    makeObservable(this, {
      valueColor: observable,
      tdBgColor: observable,
      //totalValue: observable,
      isHaveWarning: observable,
      tls_LevelGroup_Descriptions: observable,
      vcs_Item: observable
    })
  }

  _id
  get id() {
    if (!this._id) {
      this._id = genUtil.randomId();
    }
    return this._id;
  }

  get ColumnIndex() {
    return this.Column.Index;
  }

  items = []
  get Items() {
    return this.items;
  }
  set Items(v) {
    this.items = v;
  }

  unit
  get Unit() {
    if (!this.unit) {
      this.unit = this.GetUnit();
    }
    return this.unit;
  }
  set Unit(v) {
    this.unit = v;
  }

  GetUnit = () => {
    if (this.SubCells) {
      var unit = undefined;
      this.SubCells.forEach(x => {
        if (x.Unit) {
          unit = x.Unit;
        }
      })
      return unit;
    }
    return this.Items && this.Items.length !== 0 ? this.Items[0].Unit : undefined;
  }

  unitVM
  get UnitVM() {
    if (!this.unitVM) {
      this.unitVM = textVMUtil.get(this.Unit);
      this.unitVM.OnSetContent = (v) => this.Unit = v;
    }
    return this.unitVM;
  }

  OnGet_TotalValue_Func_Dict = []
  set Get_TotalValue(v) {
    this.OnGet_TotalValue_Func_Dict.forEach(x => x.Value());
  }

  totalValue = undefined
  get TotalValue() {
    // Chnh sa Value thnh FullValue (cha khi lng tng in hnh)
    //this.TotalValue = this.GetTotalValue();
    if (this.totalValue === undefined) {
      util.Handle_TotalValue(this);
    }
    return this.totalValue;
  }
  set TotalValue(v) {
    this.totalValue = v;
    this.Get_TotalValue = true;
    // Thit lp phn mu sc i vi Phase
    if (this.IsHaveVCS_Item) {
      util.Handle_VCS_Item(this);
    }

    // Thit lp gi tr Cell
    this.ContentVM.Content = this.ValueString;
  }

  contentVM
  get ContentVM() {
    if (!this.contentVM) {
      this.contentVM = textVMUtil.get(this.ValueString);
    }
    return this.contentVM;
  }

  get ValueString() {
    if (this.Row.IsCreatedByUser) return '';
    var totalValue = this.TotalValue;
    if (!totalValue) return '';

    var unit = this.Unit ? this.Unit : '';

    var formatValue = totalValue.toLocaleString('en-US', { maximumFractionDigits: 2 });
    return `${formatValue} ${unit}`;
  }

  subCells
  get SubCells() {
    if (!this.subCells) {
      this.subCells = this.GetSubCells();
    }
    return this.subCells;
  }
  set SubCells(v) {
    this.subCells = v;
  }

  GetSubCells = () => {
    var subRows = this.Row.SubItems;
    if (subRows && subRows.length !== 0) {
      var col = this.Column;
      return subRows.map(x => x.GetCellByColumn(col))
        .filter(x => x !== undefined);
    }
  }

  massType
  get MassType() {
    if (!this.massType) {
      this.massType = util.GetMassType(this);
    }
    return this.massType;
  }
  set MassType(v) {
    this.massType = v;
  }

  AsString = () => {
    console.log(`${this.Column.Name} - ${this.Row.Name} - ${this.TotalValue}`)
  }

  UpdateData = () => {
    if (this.Row.IsCreatedByUser) {
      return;
    }

    this.SubCells = this.GetSubCells();

    this.Unit = this.GetUnit();
    this.MassType = util.GetMassType(this);
    this.IsHaveVCS_Item = util.GetIsHaveVCS_Item(this);
  }

  ShowDetail = () => {
    var isHash = true;
    var url = `${process.env.PUBLIC_URL}${isHash ? '/#' : ''}/massdetail`;
    var getUrl = (v) => `${url}/${v}`;
    var openTabFunc = (v) =>
      window.open(getUrl(v), "_blank");

    if (!this.viewDetailId) {
      this.GetViewDetailId(data => {
        var _id = this.viewDetailId = data._id;
        openTabFunc(_id);
      })
    } else {
      openTabFunc(this.viewDetailId);
    }
  }

  viewDetailId

  get AllItems() {
    if (this.SubCells) {
      return this.SubCells.map(x => x.Items).reduce((a, b) => a.concat(b), []);
    }
    return this.Items;
  }

  isHaveVCS_Item
  get IsHaveVCS_Item() {
    if (!this.isHaveVCS_Item) {
      this.isHaveVCS_Item = util.GetIsHaveVCS_Item(this);
    }
    return this.isHaveVCS_Item;
  }
  set IsHaveVCS_Item(v) {
    this.isHaveVCS_Item = v;
  }

  vcs_Item = undefined
  get VCS_Item() {
    return this.vcs_Item;
  }
  set VCS_Item(v) {
    this.vcs_Item = v;

    this.ValueColor = util.GetValueColor(this);
    this.TDBgColor = util.GetTDBgColor(this)
  }

  valueColor = '#1890ff'
  get ValueColor() {
    return this.valueColor;
  }
  set ValueColor(v) {
    this.valueColor = v
  }

  tdBgColor = undefined
  get TDBgColor() {
    return this.tdBgColor;
  }
  set TDBgColor(v) {
    this.tdBgColor = v;
  }

  get AStyle() {
    return {
      color: this.ValueColor,
      fontWeight: 'bold'
    }
  }

  get TDStyle() {
    return {
      backgroundColor: this.TDBgColor
    }
  }

  ApiData = () => {
    var massIds = this.AllItems.map(x => x._id);
    return {
      massType: this.MassType,
      massIds
    };
  }

  GetViewDetailId = (handle) => {
    var apiData = this.ApiData();
    var url = "https://api.bimdev.vn/api/hbdv/updatemass";
    api.put(url, apiData).then(response => response.data)
      .then(data => handle(data))
  }

  OnGet_TLS_LevelGroups_Func_Dict = []
  set Get_TLS_LevelGroups(v) {
    this.OnGet_TLS_LevelGroups_Func_Dict.forEach(x => x.Value());
  }

  tls_LevelGroups
  get TLS_LevelGroups() {
    if (!this.tls_LevelGroups) {
      util.Handle_TLS_LevelGroups(this);
    }
    return this.tls_LevelGroups;
  } set TLS_LevelGroups(v) {
    this.tls_LevelGroups = v;
    this.Get_TLS_LevelGroups = true;
  }

  isHaveWarning = undefined
  get IsHaveWarning() {
    if (this.isHaveWarning === undefined) {
      this.isHaveWarning = util.GetIsHaveWarning(this);
    }
    return this.isHaveWarning;
  } set IsHaveWarning(v) {
    this.isHaveWarning = v;
  }

  tls_LevelGroup_Descriptions = undefined
  get TLS_LevelGroup_Descriptions() {
    if (!this.tls_LevelGroup_Descriptions) {
      util.Handle_TLS_LevelGroup_Descriptions(this);
    }
    return this.tls_LevelGroup_Descriptions;
  } set TLS_LevelGroup_Descriptions(v) {
    this.tls_LevelGroup_Descriptions = v;
  }
}