import * as mt from '../MassType'

export const Project = 'Project'
export const Phase = 'Phase'
export const Workpackage = 'Workpackage'
export const Value = 'Value'
export const Unit = 'Unit'
export const partition = 'partition'
export const rebarType = 'rebarType'
export const elementName = 'elementName'
export const MassType = 'MassType'
export const workItem = 'workItem'
export const level = 'level'
export const diameter = 'diameter'
export const block = 'block'
export const discipline = 'discipline'

export const GetAll = () => {
  return [MassType, workItem, level, diameter]
}

export const GetDirectiveFields = () => {
  return [Project, Workpackage, Value, Unit];
}

export const GetIdentifyFields = () => {
  return [elementName];
}

export const GetFullFields = (massItem, field) => {
  switch (field) {
    case Project:
    case Value:
    case Unit:
    case MassType:
    case Phase:
      return {
        fields: [field],
        isFromElement: false
      };
    case workItem:
    case elementName:
    case rebarType:
    case partition:
    case level:
    case diameter:
      var massType = massItem[MassType];
      if (massType === mt.rebar) {
        return {
          fields: ['Info', field],
          isFromElement: false
        };
      } else {
        var groupName = undefined;
        // kim tra cc phn nhm trong Element: identify, locate ...
        switch (field) {
          case workItem:
          case elementName:
            groupName = 'identify';
            break;
          case level:
            groupName = 'locate';
            break;
        }
        return {
          fields: ['Element', 'info', groupName, field],
          isFromElement: true
        }
      }
  }
}

export const GetValueString = (massFieldValue, field) => {
  var value = undefined;

  switch (field) {
    case Project:
    case Phase:
      value = massFieldValue.Name;
      break;
    case Workpackage:
      value = massFieldValue.FullName;
      break;
    default:
      value = massFieldValue;
      break;
  }

  return value ? value : 'undefined';
}

export const GetListForTypicalLevelFilter = () => {
  return [workItem, block, discipline]
}

export const GetDefaultListValue = (field) => {
  switch (field) {
    case workItem:
      return ['Dm', 'Sn', 'Ct', 'Vch'];
    default:
      return [];
  }
}