import { makeObservable, observable, action } from 'mobx';
import * as util from './TableRowUtil'
import * as rs from './RenderState'
import * as genUtil from '../../util/generalUtil'

export default class TableRow {
  key
  get Key() {
    if (!this.key) {
      this.key = this.Item._id ? this.Item._id : this.Item.id;
      if (!this.key){
        this.key = genUtil.randomId();
      }
    }
    return this.key;
  }

  cells
  get Cells() {
    if (!this.cells) {
      this.cells = util.GetCells(this);
    }
    return this.cells;
  }

  Edit = () => {
    this.Cells.forEach(x => x.RenderState = rs.edit);
  }

  OK = () => {
    this.Cells.forEach(x => x.RenderState = rs._default);
    this.Table.EditItem(this.Item);
  }
}