import * as api from  '../../../../util/apiUtil'
import { ExportSetVersion } from "./ExportSetVersion";

export const get = (data) => {
  var qI = new ExportSetVersion();
  qI._id = data._id;
  return qI;
}

export const getLastestByExportSet = (exportSetId, handle) => {
  if (!exportSetId) return;
  var url = `https://api.bimdev.vn/api/hbesv/last?exportSetId=${exportSetId}`;
  api.get(url).then(response => response.data)
    .then(data => {
      var item = data ? get(data) : data;
      handle(item);
    });
}

export const add = (apiData, handle) => {
  var url = `https://api.bimdev.vn/api/hbesv/add`;
  api.post(url, apiData).then(response => response.data)
    .then(data => {
      var item = get(data);
      handle(item);
    });
}