import * as api from '../../../../util/apiUtil'
import Project from './Project';
import * as rebarMassDictUtil from '../mass/rebar/RebarMass_DictUtil'
import * as exportSetUtil from '../exportset/ExportSetUtil'
import * as vrf from '../value/ValueRetrieveFrom'
import * as levelDictUtil from '../level/Level_DictUtil'
import * as levelTypicalUtil from '../setting/typicallevel/TypicalLevelSettingUtil'

// Instance
export const get = (q, dict) => {
  if (!q) return undefined;

  var qI = new Project();

  qI._id = q._id;
  qI.Name = q.name;
  qI.Code = q.code;
  qI.Dict = dict;

  return qI;
}

export const getAll = (dict, handle) => {
  var url = "https://api.bimdev.vn/api/hbp/list";
  api.get(url).then(response => response.data)
    .then(data => {
      var items = data.map(x => get(x, dict))
      if (handle) {
        handle(items);
      }
    });
}

export const getById = (_id, handle) => {
  if (!_id) return undefined;
  var url = `https://api.bimdev.vn/api/hbp/get?id=${_id}`;
  api.get(url).then(response => response.data)
    .then(data => {
      var item = get(data);
      if (handle) {
        handle(item);
      }
    });
}

// Property
export const GetRebarMass_Dict = (q) => {
  return rebarMassDictUtil.get(q);
}

export const GetInputExportSet_Async = (q) => {
  var projectId = q._id;

  exportSetUtil.getByProject(projectId, (item) => {
    if (!item) {
      var apiData = {
        projectId,
        name: 'D liu khi lng input',
        description: 'm t d liu',
        retrieveFrom: vrf.input
      }
      exportSetUtil.add(apiData, (addItem) => {
        q.InputExportSet = addItem;
      })
    } else {
      q.InputExportSet = item;
    }
  })
}

export const GetLevel_Dict = (q) => {
  return levelDictUtil.get(q)
}

export const GetTypicalLevelSetting = (q) => {
  return levelTypicalUtil.GetTest(q);
}