import React, { Component } from 'react';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Row, Tabs } from 'antd';
import { TypicalLevelSettingDataVM } from './TypicalLevelSettingDataVM';
import LevelGroupView from './levelGroup/LevelGroupView';
import './TypicalLevelSettingData.css'
import './pageItem/TLS_PageItem.css'
import TLS_FilterView from './filter/TLS_FilterView';
import TLS_PageItemView from './pageItem/TLS_PageItemView';

const { TabPane } = Tabs;

class TypicalLevelSettingDataView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new TypicalLevelSettingDataVM();
    } else {
      this.state = store;
    }
  }

  onKeyDown = (v) => {
    //console.log(v);
    v.stopPropagation();
  }

  render() {
    var data = this.state;

    return (
      <>
        <div>
          <div className='tls__tabs__div'>
            {/* Add Tab */}
            {!data.IsHaveAddCommandTab ?
            <Button type='primary' size='small' onClick={data.AddPageItem}
              className='tls__tabs__add__outside'>+</Button>
              : ''}

            {/* Tabs */}
            <Tabs type='card' className='tls__tabs'
              onChange={data.ChangePageKey}
              activeKey={data.CurrentPageKey}
              onKeyDown={this.onKeyDown}>
              {data.AllTabs.map(x => {
                return <TabPane tab={x.TitleElement} key={x.id}>
                  <TLS_PageItemView store={x} />
                </TabPane>
              })}
            </Tabs>
          </div>
        </div>
      </>
    );
  }
}

export default observer(TypicalLevelSettingDataView);
