import { makeObservable, observable, action } from 'mobx';
import * as rs from './RenderState'

export default class TableCell {
  constructor() {
    makeObservable(this, {
      renderState: observable
    })
  }

  get Key() {
    return `${this.Column.Key}_${this.Row.Key}`;
  }

  get Item() {
    return this.Row.Item;
  }

  renderState = undefined
  get RenderState() {
    if (!this.renderState){
      this.renderState = this.Column.RenderState;
    }
    return this.renderState;
  }
  set RenderState(v){
    this.renderState = v;
  }

  render_Dict
  get Render_Dict() {
    if (!this.render_Dict) {
      this.render_Dict = this.Column.Render_Dict;
    }
    return this.render_Dict;
  }

  get DataIndex() {
    return this.Column.DataIndex;
  }

  Render = () => {
    var dataIndex = this.DataIndex;
    var item = this.Item;

    var value = undefined;

    if (dataIndex) {
      value = item;
      dataIndex.forEach(x => {
        value = value[x]
      })
    }

    var render = this.Render_Dict.GetRender(this.RenderState);
    return render.Render(value, item, dataIndex, this, this.Row);
  }
}