import { PMData } from '../../../single/model/PMData';
import MassGroupSettingData from './MassGroupSettingData';
import * as mpn from '../../../entity/mass/MassPropertyName'
import * as massGroupCompUtil from '../../mass/table/rowgroup/MassGroupComponentUtil'

export const pmData = ()  =>{
  return PMData.Instance;
}

export const get = (q) => {
  var qI = new MassGroupSettingData();
  qI.MassGroupComponent = massGroupCompUtil.get(q.massGroupComponent);
  return qI;
}

export const GetDefault = () => {
  var qI = new MassGroupSettingData();
  return qI;
}

export const GetDefaultFields = () => {
  return [ mpn.Category ]
}