import { Level_Dict } from "./Level_Dict"
import * as itemUtil from './LevelUtil'

export const get = (project) => {
  var qI = new Level_Dict();
  qI.Project = project;

  itemUtil.getAll({ projectId : project._id }, qI, (items) => {
    qI.Items = items.sort((a,b) => a.Elevation - b.Elevation);
    qI.GetItemsDone = true;
  })

  return qI;
}