import { PMDataVM } from '../../../single/form/PMDataVM';
import { TypicalLevelSettingDataVM } from './TypicalLevelSettingDataVM';
import * as pageItemUtil from './pageItem/TLS_PageItemVMUtil'

export const pmDataVM = () => {
  return PMDataVM.Instance;
}

export const get = (q, dict) => {
  var qI = new TypicalLevelSettingDataVM();
  qI.ModelItem = q;
  qI.Dict = dict;

  return qI;
}

export const GetPageItems = (q) => {
  return q.ModelItem.PageItems.map(x => pageItemUtil.get(x, q));
}