import TableColumn from "./TableColumn"
import * as renderUtil from './RenderItemUtil'
import { Button } from "antd";
import * as rs from './RenderState'
import * as genUtil from '../../util/generalUtil'

export const get = (title, dataIndex, renders, table) => {
  var qI = new TableColumn();
  qI.Title = title;
  qI.DataIndex = dataIndex;
  qI.Key = genUtil.randomId();
  qI.Table = table;

  var dict = qI.Render_Dict;
  if (renders) {
    renders.forEach(x => {
      dict.GetRender(x.State).Render = x.Render
    });
  }

  return qI;
}

export const GetActionColumn = (table) => {
  var title = undefined;
  if (table.CanAdd) {
    var add = table.AddItem;

    title = <Button type='primary' size='small'
      onClick={add}>Add</Button>
  }

  var renders = [
    renderUtil.get(rs._default, (val, item, dataIndex, cell, row) => {
      var editElem = '';
      if (table.CanEdit) {
        var edit = () => row.Edit();
        editElem = <Button type='primary' size='small'
          style={{ backgroundColor: '#faad14', border: 'none' }}
          onClick={edit}>Edit</Button>
      }

      var delElem = '';
      if (table.CanDelete) {
        var del = () => table.DeleteItem(item, row);

        delElem = <Button type='primary' danger size='small'
          onClick={del}>Delete</Button>
      }
      return <>
        {editElem}{delElem}
      </>
    }),
    renderUtil.get(rs.edit, (val, item, dataIndex, cell, row) => {
      var ok = () => row.OK();
      return <Button type='primary' size='small'
        onClick={ok}>OK</Button>
    })
  ]

  return get(title, null, renders, table);
}