import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Input } from "antd";
import * as textVMUtil from '../text/TextVMUtil'
import * as ts from '../text/TextState'

class InputView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      var content = `Ni dung input`
      this.state = textVMUtil.get(content);
    } else {
      this.state = store;
    }
  }

  onChange = (e) => {
    var { value } = e.target;
    this.state.Content = value;
  }

  render() {
    var data = this.state;
    var { content, placeholder } = data;

    return (
      <>
        {data.State === ts.text ?
          <Input value={content} onChange={this.onChange}
            placeholder={placeholder} /> : ''}
        {data.State === ts.password ?
          <Input.Password value={content} onChange={this.onChange} /> : ''}
      </>
    )
  }
}

export default observer(InputView)