// }

export const Handle_Title = (q) => {
  var field = q.MassGroupItem.Field;
  if (field !== mif.level) {
    q.Title = q.Name;
  } else {
    // X l phn tiu  ca Row da trn Name v thng tin Tng in hnh
    var list = [];
    var handledCount = 0;
    var count = q.Cells.length;

    const handle_LS = (ls) => {
      if (ls.Description) {
        q.Title = ls.Description;
      } else {
        ls.OnGetLevelStorageList_Funcs.push(() => {
          q.Title = ls.Description;
        })
      }
    }

    const handle_Val = (val) => {
      handledCount++;
      list.push.apply(list, val);
      if (handledCount === count) {
        if (list.length === 1) {
          handle_LS(list[0]);
        } else {
          q.Title = q.Name;
        }
      }
    }

    q.Cells.forEach(x => {
      if (x.TLS_LevelGroups) {
        handle_Val(x.TLS_LevelGroups);
      } else {
        x.OnGet_TLS_LevelGroups_Funcs.push(() => {
          handle_Val(x.TLS_LevelGroups);
        })
      }
    })
  }
}

export const GetLocationSelectVM = (q) => {
  var obj = selectUtil.get([], undefined);
  obj.Placeholder = 'chn v tr'
  obj.TitleRender = (item) => {
    var marginLeft = item.Rank * 15;
    return <div
      style={{ marginLeft }}>
      {item.Title}
    </div>
  }
  obj.OnSelectedItemChanged = (v) => {
    var factory = q.ReplaceFactory;
    factory.ReferenceRow = v;
  }
  return obj;
}

export const GetSubMassGroupItem = (q) => {
  var subItems = q.SubItems;
  if (subItems.length === 0) return undefined;
  return subItems[0].MassGroupItem;
}

export const GetSubFields = (q) => {
  var subMGI = q.SubMassGroupItem;
  if (!subMGI) return [];
  var mgis = subMGI.SupComponent.Items;

  var lastValidIndex = -1;
  var isValidHandle = true
  mgis.forEach((x, i) => {
    if (isValidHandle && x.HaveMassItemSubComponents.length === 0) {
      lastValidIndex = i;
    } else {
      isValidHandle = false;
    }
  })

  var currentIndex = mgis.indexOf(q.MassGroupItem);
  if (lastValidIndex === -1) return [subMGI.Field];
  var fields = mgis.filter((x, i) =>
    (currentIndex === -1 || currentIndex < i) &&
    i <= lastValidIndex)
    .map(x => x.Field);
  return fields;
}

export const CreateSubMassGroupItem = (q) => {
  var filter = new EntFilter();
  filter.ValueType = fvt.bystring;
  filter.StringFilterType = sft.byequal;
  filter.Values = [q.FullValue];

  var supMgi = q.MassGroupItem;

  var mgc = undefined;
  var mgcIndex = supMgi.SubComponents
    .findIndex(x => genUtil.IsSameValues(x.Filter.Values, filter.Values));

  if (mgcIndex !== -1) {
    mgc = supMgi.SubComponents[mgcIndex]
  } else {
    mgc = new MassGroupComponent();
    mgc.SubItem = supMgi;
    mgc.Filter = filter;
    supMgi.SubComponents.push(mgc);
  }

  var mgi = mgiUtil.get(null, null, mgc);
  mgc.Items = [mgi];

  q.SubMassGroupItem = mgi;
  return mgi;
}

export const Add = (supRow, table) => {
  var mgi = supRow ?
    (supRow.SubMassGroupItem ? supRow.SubMassGroupItem :
      CreateSubMassGroupItem(supRow))
    : table.MainMGI;

  var mgi_I = mgi_iUtil.create(mgi, mgii_ct.byuser);
  mgi.Items.push(mgi_I);

  var row = get(mgi_I.Value, table);
  row.FullValue = mgi_I.Value;
  row.IsCreatedByUser = true;
  row.MassGroupItem = mgi;

  row.SupItem = supRow;
  row.CurrentList.push(row);
  return row;
}