import { makeObservable, observable, action } from 'mobx';
import * as mif from '../item/MassItemField'
import MassColumn from './MassColumn';
import * as colUtil from './MassColumnUtil'
import * as rowUtil from './MassRowUtil'
import * as cellUtil from './MassCellUtil'
import * as util from './MassTableUtil'
import { PMData } from '../../../single/model/PMData';
import * as filterUtil from '../../filter/EntFilterUtil'
import * as textUtil from '../../../../text/TextVMUtil'
import * as api from '../../../../../util/apiUtil'
import * as st from '../../setting/base/SettingType'
import { message } from 'antd';
import * as mgsUtil from '../../setting/massGroup/MassGroupSettingUtil'
import * as mgsVMUtil from '../../setting/massGroup/MassGroupSettingVMUtil'
import { PMDataVM } from '../../../single/form/PMDataVM';
import * as compUtil from './rowgroup/MassGroupComponentUtil'

export const pmData = () => {
  return PMData.Instance;
}

export const pmDataVM = () => {
  return PMDataVM.Instance;
}

export default class MassTable {
  constructor() {
    makeObservable(this, {
      IsValidTable: observable,
      Columns: observable,
      MainRows: observable,
      Cells: observable,
      columnField: observable,
      IsOnSaveMassGroup: observable
    })
  }


  items
  get Items() {
    return this.items;
  }
  set Items(v) {
    this.items = v;

    if (v && v.length !== 0) {
      this.Build();
      this.IsValidTable = true;
    }
    else {
      this.IsValidTable = false;
    }
  }

  IsValidTable = false

  columnField = mif.Project
  get ColumnField() {
    return this.columnField;
  }
  set ColumnField(v) {
    this.columnField = v;
  }

  massGroupSetting
  get MassGroupSetting() {
    return this.massGroupSetting;
  }
  set MassGroupSetting(v) {
    this.massGroupSetting = v;
    if (!v) {
      this.MainMGC = v;
    } else {
      this.MainMGC = compUtil.Clone(v.Data.MassGroupComponent);
    }
  }

  mainMGC
  get MainMGC() {
    if (!this.mainMGC) {
      this.mainMGC = util.GetMainMGC(this);
    }
    return this.mainMGC;
  }
  set MainMGC(v) {
    this.mainMGC = v;
    this.Init();
    if (!v) {
      this.MainMGI = undefined;
    } else {
      this.MainMGI = util.GetMainMGI(this);
    }
  }

  mainMGI
  get MainMGI() {
    if (!this.mainMGI) {
      this.mainMGI = util.GetMainMGI(this);
    }
    return this.mainMGI;
  }
  set MainMGI(v) {
    this.mainMGI = v;
  }

  Columns = undefined
  FullRows = undefined
  MainRows = undefined
  Cells = undefined

  Init = () => {
    this.Columns = [];
    this.MainRows = [];
    this.FullRows = [];
    this.Cells = [];
  }

  get AllRows() {
    return util.GetAllRows(this);
  }

  GetColumn = (v) => {
    var list = this.Columns;
    var item = undefined;
    var index = list.findIndex(x => x.Name === v);
    if (index === -1) {
      item = colUtil.get(v, this);
      list.push(item);
      return item;
    }
    else {
      return list[index];
    }
  }

  GetRow = (v, supItem) => {
    var list = undefined;
    if (supItem) {
      list = supItem.SubItems;
    } else {
      list = this.MainRows;
    }

    var item = undefined;
    var index = list.findIndex(x => x.Name === v);
    if (index === -1) {
      item = rowUtil.get(v, this);
      item.SupItem = supItem;

      list.push(item);
      this.FullRows.push(item);
      return item;
    }
    else {
      return list[index];
    }
  }

  GetCell = (col, row) => {
    var list = this.Cells;
    var item = undefined;
    var index = list.findIndex(x => x.Column === col && x.Row === row);
    if (index === -1) {
      item = cellUtil.get(col, row, this);
      list.push(item);
      return item;
    }
    else {
      return list[index];
    }
  }


  Build = () => {
    var colField = this.ColumnField;

    if (colField === mif.Phase) {
      pmData().MassResultComp.Phases.forEach(x => {
        var col = this.GetColumn(mif.GetValueString(x, colField));
      })
    }

    this.Items.forEach(x => {
      switch (colField) {
        case mif.Phase:
          x.ValidPhasesByInput.forEach(y => {
            this.HandleItem(x, y);
          })
          break;
        default:
          this.HandleItem(x, x.GetValue(colField));
          break;
      }
    })

    if (!this.IsHaveAsyncData) {
      this.UpdateData();
    }
  }

  IsHaveAsyncData = false;
  IsOnSaveMassGroup = false;

  HandledItems = []
  HandledItemPhases = []

  OnSetHandledRatio_Funcs = []

  handledRatio
  get HandledRatio() {
    return this.handledRatio;
  } set HandledRatio(v) {
    this.handledRatio = v;
    this.OnSetHandledRatio_Funcs.forEach(x => x(v));
  }

  AddHandledItems = (item) => {
    if (this.ColumnField !== mif.Phase) {
      this.HandledItems.push(item);
    } else {
      var list = this.HandledItemPhases;
      var index = list.findIndex(x => x.Item === item);
      var obj = undefined;
      if (index === -1) {
        obj = {
          Item: item,
          Count: 0
        };
        list.push(obj);
      } else {
        obj = list[index];
      }
      obj.Count++;

      if (obj.Count === item.ValidPhasesByInput.length){
        this.HandledItems.push(item);
      }
    }

    this.HandledRatio = this.HandledItems.length / this.Items.length;

    if (this.HandledItems.length === this.Items.length) {
      this.UpdateData();
      this.HandledItems = [];
    }
  }

  UpdateData = () => {
    this.MainRows.forEach(x => x.UpdateData());
    this.MainRows = rowUtil.Order(this.MainRows);

    this.Cells.forEach(x => x.UpdateData());
    this.IsUpdateDataDone = true;
  }

  OnIsUpdateDataDone_Funcs = []

  set IsUpdateDataDone(v) {
    this.OnIsUpdateDataDone_Funcs.forEach(x => x());
  }

  HandleItem = (item, colFieldValue, mgc, initialRow) => {
    var col =
      this.GetColumn(mif.GetValueString(colFieldValue, this.ColumnField));
    var rows = initialRow ? [initialRow] : [];
    var mgc = mgc ? mgc : this.MainMGC;

    this.HandleRowFieldIndex(item, col, rows, mgc, 0);
  }

  // X l to Row vi gi tr tng ng v Cell bn trong
  GetRow_Value_FieldIndex = (item, col, rows, val, mgc, rowFieldIndex) => {
    var mgis = mgc.Items;
    var itemCount = mgis.length;
    var mgi = mgis[rowFieldIndex];

    var lastRow = rows.length !== 0 ? rows[rows.length - 1] : undefined
    mgi.SupRow = lastRow;

    var rowField = mgi.Field;
    if (!rowField) {
      this.AddHandledItems(item);
      return;
    }

    var valueString = mif.GetValueString(val, rowField);
    var fullValue = lastRow ? `${lastRow.FullValue}__${valueString}` : valueString;

    if (mgi.NotIncludeValues.includes(fullValue)) {
      this.AddHandledItems(item);
      return;
    }

    var row = undefined;
    const addRow = (supRow) => {
      return this.Handle_AddRow(fullValue, supRow);
    }

    const handleRow = (row) => {
      row.MassGroupItem = mgi;
      rows.push(row);

      var subMgcs = mgi.SubComponents;
      var subIndex = subMgcs.findIndex(x =>
        filterUtil.IsValid(x.Filter, fullValue));
      if (subIndex !== -1) {
        mgc = subMgcs[subIndex];
        mgc.IsHaveMassItem = true;

        rowFieldIndex = -1;
        itemCount = mgc.Items.length;

        row.SubMassGroupComponent = mgc;
      }

      var cell = this.GetCell(col, row);
      cell.Items.push(item);

      if (rowFieldIndex === itemCount - 1) {
        this.AddHandledItems(item);
      } else {
        this.HandleRowFieldIndex(item, col, rows, mgc, rowFieldIndex + 1);
      }
    }

    var mgiItem = mgi.GetItem(fullValue, valueString);
    var replaceMgi = mgiItem.NewMGI;
    if (replaceMgi) {
      // Thm Value-Title cho MassGroupItem
      replaceMgi.GetItem(fullValue, valueString);

      this.Handle_ReplaceMGI(replaceMgi, addRow, handleRow);
    } else {
      row = addRow(lastRow);
      handleRow(row);
    }
  }

  Handle_AddRow = (fullValue, supRow) => {
    var r1 = this.GetRow(fullValue, supRow);
    r1.FullValue = fullValue;
    return r1;
  }

  Handle_ReplaceMGI = (replaceMgi, addRow, handleRow) => {
    const awaitGetSupRowForAdd = () => {
      var supRow = replaceMgi.SupRow;
      var r2 = addRow(supRow);
      r2.ReplaceMGI = replaceMgi;

      return r2;
    }

    var row = undefined
    if (replaceMgi.IsFirstSetSupRow) {
      row = awaitGetSupRowForAdd();
      handleRow(row);
    } else {
      replaceMgi.OnIsFirstSetSupRow_Funcs.push(() => {
        row = awaitGetSupRowForAdd();
        handleRow(row);
      })
    }
  }

  HandleRowFieldIndex = (item, col, rows, mgc, rowFieldIndex) => {
    var rowField = mgc.Items[rowFieldIndex].Field;

    var val = item.GetValue(rowField, (val) =>
      this.GetRow_Value_FieldIndex(item, col, rows, val, mgc, rowFieldIndex));
    if (!Array.isArray(val)) {
      this.GetRow_Value_FieldIndex(item, col, rows, val, mgc, rowFieldIndex);
    } else {
      this.IsHaveAsyncData = true;
    }
  }

  SaveMassGroup = () => {
    this.IsOnSaveMassGroup = true;
  }

  massGroupName = undefined
  get MassGroupName() {
    if (!this.massGroupName) {
      this.massGroupName = util.GetMassGroupName(this);
    }
    return this.massGroupName;
  }
  set MassGroupName(v) {
    this.massGroupName = v;
  }

  massGroupNameVM
  get MassGroupNameVM() {
    if (!this.massGroupNameVM) {
      var obj = this.massGroupNameVM =
        textUtil.get(this.MassGroupName, 'Tn set khi lng');
      obj.onSetContent = (v) => this.MassGroupName = v;
    }
    return this.massGroupNameVM;
  }

  MassGroupInputOK = () => {
    var items = pmData().MassGroupSetting_Dict.Items;
    var index = items.findIndex(x => x.Name === this.MassGroupName);
    var _id = undefined;
    if (index !== -1) {
      _id = items[index]._id;
    }

    var apiData = {
      _id,
      name: this.MassGroupName,
      settingType: st.massgroup,
      data: {
        massGroupComponent: this.MainMGC.ApiData
      }
    };

    var url = `https://api.bimdev.vn/api/hbs/mg`;

    var command = _id ? 'edit' : 'add';
    const apiFunc = () => _id ? api.put(`${url}/${command}`, apiData) :
      api.post(`${url}/${command}`, apiData);

    apiFunc().then(response => response.data)
      .then(data => {
        var dict = pmData().MassGroupSetting_Dict;
        var item = mgsUtil.get(data, dict);
        dict.Items.push(item);

        var dictVM = pmDataVM().MassGroupSetting_DictVM;
        var itemVM = mgsVMUtil.get(item, dictVM);
        dictVM.Items.push(itemVM);

        message.success(`${command} massgroup successfully`);
      })

    this.IsOnSaveMassGroup = false;
    this.MassGroupNameVM.Content = util.GetMassGroupName(this);
  }

  MassGroupInputCancel = () => {
    this.IsOnSaveMassGroup = false;
    this.MassGroupNameVM.Content = util.GetMassGroupName(this);
  }

  AddMainRow = () => {
    return rowUtil.Add(undefined, this);
  }
}