import * as textUtil from "../../../../../text/TextVMUtil";
import * as genUtil from "../../../../../../util/generalUtil";
import * as it from './TLS_FilterValue_InputType'
import { makeObservable, observable, action } from 'mobx';
import NewInputView from "../../../../../input/NewInputView";
import SelectView from "../../../../../select/SelectView";
import * as selectUtil from "../../../../../select/SelectVMUtil";
import * as util from './TLS_FilterValueVMUtil'

export class TLS_FilterValueVM {
  constructor() {
    makeObservable(this, {
      InputType: observable
    })
  }

  _id
  get id() {
    if (!this._id) {
      this._id = genUtil.newGUID();
    }
    return this._id;
  }

  get Index() {
    return this.Dict.Values.indexOf(this);
  }

  get Values() {
    return this.Dict.ModelItem.Values;
  }

  get Value() {
    return this.Values[this.Index];
  }
  set Value(v) {
    this.Values[this.Index] = v;
  }

  valueVM
  get ValueVM() {
    if (!this.valueVM) {
      var obj = this.valueVM = textUtil.get(this.Value);
      obj.onSetContent = (v) => {
        this.Value = v;
      }
    }
    return this.valueVM;
  }

  selectVM
  get SelectVM() {
    if (!this.selectVM) {
      var list = ['Ct', 'Vch', 'Dm', 'Sn']
      var obj = this.selectVM = selectUtil.get(list, this.Value);
      obj.Placeholder = '...'
      obj.OnSetOpen = (v) => this.SelectOpen = v;
      obj.OnSelectedItemChanged = (v) => this.Value = v;
    }
    return this.selectVM;
  }

  Delete = () => {
    this.Dict.ModelItem.Values.splice(this.Index, 1);
    this.Dict.Values.splice(this.Index, 1);
  }

  InputType = it.select

  ToggleInputType = () => {
    switch (this.InputType) {
      case it.input:
        this.SelectVM.InitialItem = this.Value;
        this.Value = this.SelectVM.SelectedItem;
        this.InputType = it.select
        break;
      case it.select:
        if (this.Value) {
          this.ValueVM.Content = this.Value;
        } else{
          if (!this.ValueVM.Content){
            this.ValueVM.Content = 'value'
          }
          this.Value =this.ValueVM.Content;
        }

        this.InputType = it.input
        break;
    }
  }

  SwitchChecked_Funcs = () => this.InputType === it.select;

  get InputElement() {
    switch (this.InputType) {
      case it.input:
        return <NewInputView store={this.ValueVM} className='tls_filter__value__input' />
      case it.select:
        return <SelectView store={this.SelectVM} className='tls_filter__value__select' />
    }
  }

  selectOpen
  get SelectOpen() {
    if (this.selectOpen === undefined) {
      this.selectOpen = this.SelectVM.Open;
    }
    return this.selectOpen;
  }
  set SelectOpen(v) {
    this.selectOpen = v;
    this.PopoverState.Content = util.GetPopoverState(this);
  }

  popoverState
  get PopoverState() {
    if (!this.popoverState) {
      this.popoverState = textUtil.get(util.GetPopoverState(this));
    }
    return this.popoverState;
  }
}