import Column from "antd/lib/table/Column";
import { PMData } from "../../../single/model/PMData";
import MassCell from "./MassCell"
import * as mif from '../item/MassItemField'
import * as filterUtil from '../../filter/EntFilterUtil'
import * as genUtil from '../../../../../util/generalUtil'

export const pmData = () => {
  return PMData.Instance;
}

export const get = (col, row, dict) => {
  var qI = new MassCell();
  qI.Column = col;
  qI.Row = row;
  qI.Dict = dict;

  col.Cells.push(qI);
  row.Cells.push(qI);

  return qI;
}

export const GetIsHaveVCS_Item = (q) => {
  return q.Dict.ColumnField === mif.Phase
    && pmData().MassResultComp.Phases.length === 2
    && q.ColumnIndex === 1;
}

export const GetValueColor = (q) => {
  if (q.IsHaveVCS_Item && q.VCS_Item) {
    return '#FFFFFF';
  } else {
    return '#1890ff';
  }
}

export const GetTDBgColor = (q) => {
  if (q.IsHaveVCS_Item && q.VCS_Item) {
    return q.VCS_Item.Color;
  } else {
    return '#FFFFFF';
  }
}

// Lin quan ti vic ly TotalValue
export const Handle_VCS_Item = (q) => {
  var list = pmData().VersionCompareSetting.Items;

  var cells = q.Row.Cells;
  var compareIndex = cells.findIndex(x => x.ColumnIndex === 0);
  if (compareIndex === -1) {
    q.VCS_Item = undefined;
    return
  }
  var compareItem = cells[compareIndex];

  const getItem = () => {
    var ratio = q.TotalValue / compareItem.TotalValue;
    var index = list
      .findIndex(x => filterUtil.IsValid(x.Filter, ratio));
    return index !== -1 ? list[index] : undefined;
  }

  if (compareItem.TotalValue !== undefined) {
    q.VCS_Item = getItem();
  } else {
    genUtil.Add(compareItem.OnGet_TotalValue_Func_Dict, () =>
      q.VCS_Item = getItem())
  }
}

export const GetMassType = (q) => {
  var massType = undefined
  if (q.SubCells) {
    var isPending = true;
    q.SubCells.forEach(x => {
      if (isPending && x.MassType) {
        massType = x.MassType;
        isPending = false;
      }
    })
  } else {
    if (q.Items && q.Items.length > 0) {
      massType = q.Items[0].MassType;
    }
  }
  return massType;
}

export const Handle_TLS_LevelGroups = (q) => {
  if (q.IsGet_TLS_LevelGroups) return undefined;
  q.IsGet_TLS_LevelGroups = true;

  var list = [];
  var handledCount = 0;
  var count = q.SubCells ?
    q.SubCells.length : q.Items.length;

  const checkadd_Val = (val, list) => {
    if (list.findIndex(x => x.id === val.id) === -1) {
      list.push(val);
    }
  }

  const handle_Val = (val) => {
    handledCount++;
    if (val) {
      if (Array.isArray(val)) {
        val.forEach(x => checkadd_Val(x, list));
      } else {
        checkadd_Val(val, list)
      }
    }
    if (handledCount === count) {
      q.TLS_LevelGroups = list;
    }
  }

  if (q.SubCells) {
    // Tn ti sub cells
    q.SubCells.forEach(x => {
      if (x.TLS_LevelGroups) {
        handle_Val(x.TLS_LevelGroups);
      } else {
        genUtil.Add(x.OnGet_TLS_LevelGroups_Func_Dict,
          () => handle_Val(x.TLS_LevelGroup));
      }
    })
  } else {
    // Khng c sub cells
    q.Items.forEach(x => {
      var targetObj = x;

      // Kim tra value theo Phase hay theo Property khc (Project ...)
      var table = q.Dict;
      var colField = table.ColumnField;
      if (colField === mif.Phase) {
        // Khi lng theo Phase
        targetObj = x.GetPhaseItem(q.Column.Name);
      } else {
        // Khi lng theo cc thuc tnh khc
      }

      if (targetObj.TLS_LevelGroup || targetObj.Get_TLS_LevelGroup) {
        handle_Val(targetObj.TLS_LevelGroup);
      } else {
        genUtil.Add(targetObj.OnGet_TLS_LevelGroup_Func_Dict,
          () => handle_Val(targetObj.TLS_LevelGroup));
      }
    })
  }
}

export const Handle_TotalValue = (q) => {
  if (q.IsGet_TotalValue) return undefined;
  q.IsGet_TotalValue = true;

  var checkedList = [];
  var totalVal = 0;
  var handledCount = 0;
  var count = q.SubCells ?
    q.SubCells.length : q.Items.length;

  const handle_Val = (val) => {
    handledCount++;

    if (!Number.isNaN(val)) {
      totalVal += val;
    }
    checkedList.push(val);

    if (handledCount === count) {
      q.TotalValue = totalVal;
    }
  }

  if (q.SubCells) {
    // Tn ti sub cells
    q.SubCells.forEach(x => {
      if (x.TotalValue) {
        handle_Val(x.TotalValue)
      } else {
        genUtil.Add(x.OnGet_TotalValue_Func_Dict,
          () => {
            handle_Val(x.TotalValue)
          });
      }
    })
  } else {
    // Khng c sub cells
    q.Items.forEach(x => {
      var targetObj = x;

      // Kim tra value theo Phase hay theo Property khc (Project ...)
      var table = q.Dict;
      var colField = table.ColumnField;
      if (colField === mif.Phase) {
        // Khi lng theo Phase
        targetObj = x.GetPhaseItem(q.Column.Name);
      } else {
        // Khi lng theo cc thuc tnh khc
      }

      if (targetObj.FullValue) {
        handle_Val(targetObj.FullValue)
      } else {
        genUtil.Add(targetObj.OnGet_FullValue_Func_Dict,
          () => {
            handle_Val(targetObj.FullValue)
          });
      }
    })
  }
}

export const GetIsHaveWarning = (q) => {
  if (q.Row.Field !== mif.level) {
    return false
  } else {
    const getWarningFunc = (levelGroups) => {
      return levelGroups.length !== 0;
    }

    if (q.TLS_LevelGroups) {
      return getWarningFunc(q.TLS_LevelGroups);
    } else {
      genUtil.Add(q.OnGet_TLS_LevelGroups_Func_Dict,
        () => {
          q.IsHaveWarning = getWarningFunc(q.TLS_LevelGroups)
        });
    }
  }
}

export const Handle_TLS_LevelGroup_Descriptions = (q) => {
  if (q.IsGet_TLS_LevelGroup_Descriptions) return undefined;
  q.IsGet_TLS_LevelGroup_Descriptions = true;

  const handle_Val = (val, list, handledCount, count) => {
    handledCount++;
    if (list.indexOf(val) === -1) {
      list.push(val);
    }
    if (handledCount === count) {
      q.TLS_LevelGroup_Descriptions = list;
    }
  }

  const getDescFunc = (levelGroups) => {
    var list = [];
    var handledCount = 0;
    var count = levelGroups.length

    levelGroups.forEach(x => {
      if (x.FullDescription) {
        handle_Val(x.FullDescription, list, handledCount, count);
      } else {
        //  false, x l nhiu Funcs hn, nhng thc thi thnh cng :v
        genUtil.Add(x.OnGetLevelStorageList_Func_Dict, () => {
          handle_Val(x.FullDescription, list, handledCount, count)
        }, false)
      }
    })
  }

  if (q.TLS_LevelGroups) {
    getDescFunc(q.TLS_LevelGroups)
  } else {
    q.OnGet_TLS_LevelGroups_Funcs.push(() => {
      getDescFunc(q.TLS_LevelGroups)
    })
  }
}