import { ExportSet } from "./ExportSet"
import * as api from  '../../../../util/apiUtil'
import * as versionUtil from './ExportSetVersionUtil'

export const get = (data) => {
  var qI = new ExportSet();
  qI._id = data._id;
  qI.Name = data.name;
  qI.Description = data.description;
  qI.RetrieveFrom = data.retrieveFrom;
  return qI;
}

export const GetCurrentVersion_Async = (q) => {
  var exportSetId = q._id;

  versionUtil.getLastestByExportSet(exportSetId, (item) => {
    if (!item) {
      AddVersion_Async(q);
    } else {
      q.CurrentVersion = item;
    }
  })
}

export const AddVersion_Async = (q) => {
  var exportSetId = q._id;

  var apiData = {
    exportSetId
  }

  versionUtil.add(apiData, (addItem) => {
    q.CurrentVersion = addItem;
  })
}

export const getByProject = (projectId, handle) => {
  if (!projectId) return;
  var url = `https://api.bimdev.vn/api/hbes/input?projectId=${projectId}`;
  api.get(url).then(response => response.data)
    .then(data => {
      var item = data ? get(data) : data;
      handle(item);
    });
}

export const add = (apiData, handle) => {
  var url = `https://api.bimdev.vn/api/hbes/add`;
  api.post(url, apiData).then(response => response.data)
    .then(data => {
      var item = get(data);
      handle(item);
    });
}