import { Component } from "react";
import { makeObservable, observable, action } from 'mobx';
import { observer } from 'mobx-react';
import { Button, Col, Divider, Input, Row, Select } from "antd";
import InputView from "../../../../input/InputView";
import CheckboxView from "../../../../checkbox/CheckboxView";
import MassTable from "./MassTable";
import TextView from "../../../../text/TextView";
import MassRowView from "./MassRowView";
import './MassTable.css'

const { Option } = Select;

class MassTableView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new MassTable();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    var columns = data.Columns;
    var rows = data.MainRows;

    var thElems = columns ? columns.map((x, i) => {
      return <th scope="col" key={x.id} className='text-center masscell_th'>{x.Name}</th>
    }) : '';

    var tbodyElems = rows ? rows.map((x, i) => {
      return <MassRowView key={x.id} store={x} />
    }) : '';


    return (
      <>
        {data.IsValidTable ?
          <>
            <h4 className="text-center">
              BNG TNG HP KHI LNG
            <Button type='primary' size='small' style={{ marginLeft: 15 }}
                disabled={data.IsOnSaveMassGroup}
                onClick={data.SaveMassGroup}>s</Button>
            </h4>

            {data.IsOnSaveMassGroup ?
              <Row style={{padding:7}}>
                <Col span={4}/>
                <Col span={16} style={{display:'flex'}}>
                  <InputView store={data.MassGroupNameVM} />
                  <Button type='primary' size='small'
                    style={{marginLeft:10}}
                    onClick={data.MassGroupInputOK}>ok</Button>
                  <Button type='default' size='small'
                    onClick={data.MassGroupInputCancel}>cancel</Button>
                </Col>
              </Row> : ''}

            < table className="table table-bordered table-sm masstable">
              <thead className="thead-light">
                <tr>
                  <th scope="col" className='masstitle__th'>
                    <Button onClick={data.AddMainRow} type='primary' size='small'
                      style={{backgroundColor:'#52c41a', border:'none'}}>+</Button>
                  </th>
                  {thElems}
                </tr>
              </thead>
              <tbody>
                {tbodyElems}
              </tbody>
            </table>
          </> : ''
        }
      </>
    )
  }
}

export default observer(MassTableView)