import React, { Component } from 'react';
import { observer } from 'mobx-react';
import MultiSelectView from '../../../../../select/MultiSelectView';
import { TLS_FilterVM } from './TLS_FilterVM';
import SelectView from '../../../../../select/SelectView';
import './TLS_Filter.css'
import { DeleteFilled } from '@ant-design/icons';
import { Button } from 'antd';
import TLS_FilterValueView from './TLS_FilterValueView';

class TLS_FilterView extends Component {
  constructor(props) {
    super(props);

    var { store } = this.props;
    if (!store) {
      this.state = new TLS_FilterVM();
    } else {
      this.state = store;
    }
  }

  render() {
    var data = this.state;

    return (
      <>
        <div className='tls_filter__div'>
          {/* Field Select */}
          <SelectView store={data.FieldSelectVM} />

          <div className='tls_filter__valuediv__wrapper'>
            {/* Add Value */}
            <Button type='primary' size='small' onClick={data.AddValue}
              className='tls_filter__valuediv__addbutton'>+</Button>

            {/* Value List */}
            <div className='tls_filter__valuediv'>
              {data.Values.map(x => {
                return <TLS_FilterValueView key={x.id} store={x} />
              })}
            </div>
          </div>

          <Button size='small' type='text'
            className='tls_filter__deletebutton'
            onClick={data.Delete}>
            <DeleteFilled className='tls_filter__deleteicon' />
          </Button>
        </div>
      </>
    );
  }
}

export default observer(TLS_FilterView);
